@import url("https://fonts.googleapis.com/css2?family=Mohave:wght@300;400;700&family=Quattrocento:wght@400;700&display=swap");

.expBack {
  display: grid;
  grid-template-columns: [line1] 300px [line2] 300px [line3] auto [line4] 300px [line5] 200px [lastLine];
  grid-template-rows: [row1] 300px [row2] 300px [row3] 300px [row4] 500px [row5] 300px [row6] 200px [row7] 400px [row8] 500px [row9] 400px[row10] 250px[row11] 350px [row12] 200px [row13] 200px[row14] 400px[row15] 200px [row16] 300px [row17] 200px [row18] 500px [lastRow];

  grid-template-areas:
    /* 1 */
    "head head call call ."
    /* 2 */ " year desc desc me ."
    /* 3 */ " year1 desc1 desc1 desc1 ."
    /* 4 */ " year2 gimiregen . desc2 ."
    /* 5 */ " year3 year3 desc3 desc2 ."
    /* 6 */ "desc5 desc5 desc5 year4 desc4 "
    /* 7 */ " year5 year5 gundel desc6  desc6 "
    /* 8 */ " oyster year6 year6 desc6 desc6 "
    /* 9 */ ". . zugspitze zugspitze zugspitze"
    /* 10 */ "matterhorn matterhorn year8  . ."
    /* 11 */ ". . desc8  desc8 ."
    /* 12 */ " . desc9 desc9  year9 ."
    /* 13 */ " year10 year10 desc10 desc10 ."
    /* 14 */ " year11 year11 desc10 desc10 ."
    /* 15 */ " corsica corsica corsica corsica corsica"
    /* 16 */ " . desc12 desc12 year12 ."
    /* 17 */ " desc13 desc13 year13 me2 ."
    /* 18 */ " year14 desc14 desc14 me2 .";

  width: 100%;
  height: fit-content + 200px;

  background-color: antiquewhite;
  overflow: hidden;
}

#rock {
  position: absolute;
  left: -20%;
  height: 200vh;
}

.container {
  grid-area: me;

  width: 100%;
  height: 3800px;
}

.reveal {
  position: relative;
  transform: translateY(150px);
  opacity: 0;
  transition: 2s all ease;
}

.reveal.active {
  transform: translateY(0);
  opacity: 1;

  img {
    align-self: center;

    height: 300px;
    border-radius: 50%;

    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
      rgba(0, 0, 0, 0.22) 0px 15px 12px;
  }
}
.year {
  grid-area: year;
  color: #edd48e;
  padding-left: 80px;
  font-size: 80px;
  font-family: "Mohave", sans-serif;
  font-weight: 800;
  text-shadow: rgb(0, 0, 0) 2px 4px 6px;
}

.expCard {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;

  grid-area: desc;
  color: #000000;

  text-transform: uppercase;
  font-size: 30px;
  font-family: "Mohave", sans-serif;
  font-weight: 400;
  text-shadow: rgba(0, 0, 0, 0.3) 2px 4px 6px;

  p {
    margin: 0;
    color: black;
    font-weight: bold;
    margin-left: 5px;
    margin-right: 5px;
  }
}

.call {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #edede7dd;
  grid-area: call;
  color: #5e0a0a;
  text-transform: uppercase;

  font-size: 40px;
  font-family: "Mohave", sans-serif;
  font-weight: 400;
  text-shadow: rgba(59, 15, 15, 0.8) 2px 4px 6px;
}

.chevron {
  position: absolute;
  width: 100px;
  height: 8px;
  opacity: 0;
  transform: scale3d(0.5, 0.5, 0.5);
  animation: move 3s ease-out infinite;
  margin-top: 60px;
}

.chevron:first-child {
  animation: move 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
  content: " ";
  position: absolute;
  top: 0;
  height: 100%;
  width: 51%;
  background: #5e0a0a;
}

.chevron:before {
  left: 0;
  transform: skew(0deg, 30deg);
}

.chevron:after {
  right: 0;
  width: 50%;
  transform: skew(0deg, -30deg);
}

@keyframes move {
  25% {
    opacity: 1;
  }
  33% {
    opacity: 1;
    transform: translateY(30px);
  }
  67% {
    opacity: 1;
    transform: translateY(40px);
  }
  100% {
    opacity: 0;
    transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
  }
}

@keyframes pulse {
  to {
    opacity: 1;
  }
}

