.messageMain {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;

  width: 72%;
  height: calc(94vh - 100px);
  
  box-sizing: border-box;
  border-radius: 20px;
  margin: auto;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;

} 

.cheerUp{
    width:40%;
    height: 100%;
    background: #780206;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #ff9472, #f2709c);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ff9472, #f2709c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    border-radius: 20px 0px 0px 20px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.8) 0px 8px 16px -8px;
}

.table{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 60%;
    height: 100%;
    background: rgba(255, 255, 255, 0.418);
    border-radius: 0px 20px 20px 0px;
    padding-top: 10px;
    padding-bottom: 10px;
    box-sizing: border-box;

.record{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;

    height: 60px;
    font-size: 30px;
    
    

.contact{
    font-size: 20px;
    color: black;
    margin-left: 20px;
    
}

.profile{
    display: flex;
    justify-content: flex-start;
    align-content: center;
    color: #000000;
    font-size: 20px;
    text-transform: uppercase;
    text-shadow: none;
    padding: 0;
    margin: 10px;
   
    letter-spacing: 0px;
}

.contLineMes{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-end;
  
    width: 100%;
    height: 2px;
}

#sphereMes{
    width: 10px;
    height: 10px;
  
    border-radius: 50%;
    background-color: black;
    transform: translate(0,50%);
    box-shadow: rgba(76, 76, 76, 0.8) 2px 4px 6px;
}

#lineMes{
    width: 70%;
    height: 6%;
  
    animation: slideIn 3s;
  
    background-color: black;
    box-shadow: rgba(76, 76, 76, 0.8) 2px 4px 6px;
        }
    }
}